<template>
<div class="container">
    <div class="row border border-dark banner">
        <form v-on:submit.prevent class="heroImage"> <!--v-on:submit.prevent--> 
            <div class="row justify-content-center align-content-center">
                <div class="col-2 text-center" style="transform: translateY(60px);">
                    <input v-model="valor" type="text" id="valor" class="form-control rounded-pill" placeholder="Ingresa Respuesta">
                </div>
            </div>
            <div class="row justify-content-center align-content-center text-center">
                <div class="col-3 pt-3" style="transform: translateX(-50px);">
                    <button class="btn btn-success" @click="sumar"><svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" fill="currentColor" class="bi bi-plus-circle-fill" viewBox="0 0 16 16">
                            <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8.5 4.5a.5.5 0 0 0-1 0v3h-3a.5.5 0 0 0 0 1h3v3a.5.5 0 0 0 1 0v-3h3a.5.5 0 0 0 0-1h-3v-3z"/>
                        </svg>
                    </button>
                </div>
                <div class="col-3 pt-3" style="transform: translateX(50px);">
                    <button class="btn btn-success" @click="restar"><svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" fill="currentColor" class="bi bi-dash-circle-fill" viewBox="0 0 16 16">
                            <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM4.5 7.5a.5.5 0 0 0 0 1h7a.5.5 0 0 0 0-1h-7z"/>
                        </svg>
                    </button>
                </div>
            </div>
            <div class="text-center py-5">
                <button class="btn btn-danger" @click="borrar">Borrar</button>
            </div>
        </form>

        <div class="text-center">
            <img v-show="exito" class="img-fluid paraMover" src="../assets/globos.png" height="150" width="150" alt="">
            <img v-show="fracaso" class="img-fluid paraMover2" src="../assets/sad.png" height="150" width="150" alt="" style="transform: translateY(-250px);">
            <h2 class="text-center fw-bold">Resultado: <span v-show="boleano">{{respuesta}} - {{comparativo}}</span></h2>
        </div>
    
    </div>
</div>
</template>

<script>
//import Generador from './Generador'
import {mapMutations} from 'vuex'

export default {
    props:['value1','value2','value3'],
    components:{
        //Generador
    },
    data(){
        return{
            valor: "",
            respuesta: 0,
            signo: " ",
            boleano: false,
            comparativo: '',
            exito: false,
            fracaso: false
            //nuevo:''
        }
    },
    methods:{
        ...mapMutations(['borrar2']),
        sumar() {
            //this.respuesta = parseFloat(this.numeroR1) + parseFloat(this.numeroR2);
            this.respuesta = parseFloat(this.value1) + parseFloat(this.value2);
            this.signo = "+";
            this.boleano = true;
            this.comparar();
        },
        restar() {
            this.respuesta = parseFloat(this.value1) - parseFloat(this.value2);
            this.signo = "-";
            this.boleano = true;
            this.comparar();
        },
        borrar() {
            this.valor = "";
            this.respuesta = "";
            this.comparativo = "";
            this.boleano = false;
            this.exito = false;
            this.fracaso = false;
            this.borrar2();
        },
        comparar() {
            if (parseFloat(this.valor) === Math.abs(this.respuesta)) {
                this.comparativo = 'Felicitaciones!!';
                this.exito = true;
                this.fracaso = false;
            } else {
                this.comparativo = 'Sigue trabajando';
                this.exito = false;
                this.fracaso = true;
            }
        }
    }
    

}
</script>

<style>
.paraMover {
            position: fixed;
            animation: imagen 2s linear infinite;
            z-index: 1;
        }
        
        @keyframes imagen {
            50% {
                /*transform: rotate(360deg);*/
                transform: translateY(-250px);
            }
        }
        
        .paraMover2 {
            position: fixed;
            animation: imagen2 2s linear infinite;
            z-index: 1;
        }
        
        @keyframes imagen2 {
            50% {
                transform: rotate(90deg);
            }
        }
    .heroImage {
            background-image: url('../assets/pizarra.png');
            background-repeat: no-repeat;
            /*background-size: cover;*/
            background-position-x: center;
        }
        
        .banner {
            background-image: url('../assets/banner.jpg');
            background-repeat: no-repeat;
            background-size: cover;
        }
</style>