import { createStore } from 'vuex'

export default createStore({
    state: {
        numeroa: 0,
        numerob: 0,
        numeroc: 0
    },
    mutations: {
        borrar2(state) { state.numeroa = 0, state.numerob = 0, state.numeroc = 0 },
        aleatorio: state => {
            state.numeroa = Math.ceil(Math.random() * 10),
                state.numerob = Math.ceil(Math.random() * 10),
                state.numeroc = Math.ceil(Math.random() * 2)
        }
    },
    actions: {},
    modules: {}
})